"use client";
import { useState } from "react";
import { Menu, X } from "lucide-react";

export default function Navbar() {
    const [isOpen, setIsOpen] = useState(false);

    return (
        <nav className="flex items-center justify-between p-4 border-b bg-white shadow-md">
            <div className="text-2xl font-bold text-black flex items-center">
                <svg
                    role="img"
                    className="icon icon-LogoBetter2021"
                    width="8rem"
                    height="6rem"
                    viewBox="0 0 495 133"
                    version="1.1"
                    xmlns="http://www.w3.org/2000/svg"
                >
                    <title>Better</title>
                    <path d="M427.207 129.574H457.274V70.2463C460.072 61.2943 473.189 52.9226 492.108..." fill="currentColor"></path>
                </svg>
            </div>

            {/* Mobile Menu Button */}
            <button className="md:hidden" onClick={() => setIsOpen(!isOpen)}>
                {isOpen ? <X size={28} /> : <Menu size={28} />}
            </button>

            {/* Desktop & Mobile Menu */}
            <ul className={`flex flex-col md:flex-row md:gap-8 md:items-center md:static absolute top-16 left-0 w-full bg-white md:w-auto md:bg-transparent shadow-md md:shadow-none transition-all duration-300 ease-in-out ${isOpen ? "block" : "hidden"}`}>
                <li className="p-2 md:p-0"><a href="/" className="text-black text-lg font-medium">Home</a></li>
                {[
                    "About-Us",
                    "Mortgage-Calculator",
                    "Start",
                ].map((text) => (
                    <li key={text} className="p-2 md:p-0">
                        <a href={`/${text.toLowerCase()}`} className="text-black text-lg font-medium">{text}</a>
                    </li>
                ))}
            </ul>

            <a href="/signin" className="text-lg font-bold text-black hidden md:block">
                Sign in
            </a>
        </nav>
    );
}
